"use client";

import React, { useRef, useMemo } from "react";
import { Canvas, useFrame } from "@react-three/fiber";
import { Points, PointMaterial, Float, Stars } from "@react-three/drei";
import * as THREE from "three";

const AccretionDisk = () => {
  const pointsRef = useRef<THREE.Points>(null);
  
  // Create particles for the accretion disk
  const particlesCount = 5000;
  const positions = useMemo(() => {
    const pos = new Float32Array(particlesCount * 3);
    for (let i = 0; i < particlesCount; i++) {
      const angle = Math.random() * Math.PI * 2;
      const radius = 3 + Math.random() * 4;
      const x = Math.cos(angle) * radius;
      const y = (Math.random() - 0.5) * 0.2;
      const z = Math.sin(angle) * radius;
      pos[i * 3] = x;
      pos[i * 3 + 1] = y;
      pos[i * 3 + 2] = z;
    }
    return pos;
  }, []);

  useFrame((state) => {
    if (pointsRef.current) {
      pointsRef.current.rotation.y += 0.002;
    }
  });

  return (
    <Points ref={pointsRef} positions={positions} stride={3}>
      <PointMaterial
        transparent
        color="#8b5cf6"
        size={0.03}
        sizeAttenuation={true}
        depthWrite={false}
        blending={THREE.AdditiveBlending}
      />
    </Points>
  );
};

const EventHorizon = () => {
  return (
    <mesh>
      <sphereGeometry args={[2.5, 64, 64]} />
      <meshBasicMaterial color="#000000" />
    </mesh>
  );
};

const GlowingRing = () => {
  return (
    <mesh rotation={[Math.PI / 2, 0, 0]}>
      <torusGeometry args={[2.8, 0.05, 16, 100]} />
      <meshBasicMaterial color="#a78bfa" transparent opacity={0.5} />
    </mesh>
  );
};

export const BlackHole = () => {
  return (
    <div className="absolute inset-0 z-0">
      <Canvas camera={{ position: [0, 5, 12], fov: 45 }}>
        <color attach="background" args={["#000000"]} />
        <ambientLight intensity={0.5} />
        
        <Stars radius={100} depth={50} count={5000} factor={4} saturation={0} fade speed={1} />
        
        <Float speed={2} rotationIntensity={0.5} floatIntensity={0.5}>
          <group rotation={[0.4, 0, 0.2]}>
            <EventHorizon />
            <AccretionDisk />
            <GlowingRing />
            
            {/* Additional glow layer */}
            <mesh>
              <sphereGeometry args={[2.7, 64, 64]} />
              <meshBasicMaterial color="#4c1d95" transparent opacity={0.1} side={THREE.BackSide} />
            </mesh>
          </group>
        </Float>
      </Canvas>
    </div>
  );
};
