"use client";

import { motion } from "framer-motion";
import { useState, useEffect } from "react";
import { Menu, X } from "lucide-react";
import { cn } from "@/lib/utils";

export const Navbar = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isOpen, setIsOpen] = useState(false);

  useEffect(() => {
    const handleScroll = () => setIsScrolled(window.scrollY > 20);
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <motion.nav
      initial={{ y: -100, opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{ duration: 1 }}
      className={cn(
        "fixed top-0 left-0 right-0 z-50 transition-all duration-500 px-8 py-6",
        isScrolled ? "bg-black/20 backdrop-blur-xl border-b border-white/5 py-4" : "bg-transparent"
      )}
    >
      <div className="max-w-[1400px] mx-auto flex items-center justify-between">
        <motion.div 
          whileHover={{ scale: 1.05 }}
          className="text-xl font-black tracking-[0.2em] text-white"
        >
          VOID<span className="text-purple-500">.</span>
        </motion.div>

        <div className="hidden md:flex items-center gap-12">
          {["Vision", "Mechanism", "Intelligence"].map((item) => (
            <a
              key={item}
              href="#"
              className="text-[11px] font-bold text-gray-400 hover:text-white transition-all uppercase tracking-[0.3em] relative group"
            >
              {item}
              <span className="absolute -bottom-2 left-0 w-0 h-[1px] bg-purple-500 transition-all group-hover:w-full" />
            </a>
          ))}
        </div>

        <button className="hidden md:block px-6 py-2 border border-white/10 hover:border-white/30 text-white rounded-full text-[10px] font-black uppercase tracking-widest transition-all hover:bg-white/5">
          Join the Collective
        </button>

        <button className="md:hidden text-white" onClick={() => setIsOpen(!isOpen)}>
          {isOpen ? <X size={20} /> : <Menu size={20} />}
        </button>
      </div>

      {/* Mobile Menu */}
      {isOpen && (
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          className="fixed inset-0 bg-black z-[-1] flex flex-col items-center justify-center gap-8"
        >
          {["Vision", "Mechanism", "Intelligence"].map((item) => (
            <a
              key={item}
              href="#"
              className="text-2xl font-black text-white uppercase tracking-[0.2em]"
              onClick={() => setIsOpen(false)}
            >
              {item}
            </a>
          ))}
        </motion.div>
      )}
    </motion.nav>
  );
};
